@import url('https://fonts.googleapis.com/css2?family=DM+Sans:wght@400;700&display=swap');

body {
  padding:50px;
  background:url('../assets/images/bg.png') no-repeat center center;
  background-size:cover;
  font-family: 'DM Sans', sans-serif;
}


.box {
  display:inline-block;
  background: #FFFFFF;  
  box-shadow: 0px 0px 16px rgba(58, 78, 88, 0.05);
  border-radius: 12px;
  padding:15px 30px;
  box-sizing:border-box;
  margin:25px 0px;
}

.box.full {
  width:100%;
}


.container {
  display:block;
  padding:15px 25px;
  max-width:1250px;
  margin:0px auto;
}

.meter {
  border-radius:10px;
  display:block;
  width:100%;
  height:23px;
  background:#f8f8f8;
  max-width:900px;
}

.meter > span {
  border-radius:10px;
  background: #0C3EE3;
  height:100%;
  display:block;
  width:auto;
}

.icon {
  max-width:50px;
  width:100%;
  padding:10px;
}

.flex {
  display:flex;
  align-items: center;
}

.flex>div:last-of-type {
  min-width:150px;
  color:#5E6D7C;
  font-size:16px;
  padding:10px;
  box-sizing:border-box;
}

.icon img {
  width:100%;
  max-width:40px;
  margin:0px auto;

}



.score {
  max-width:250px;
}

.score-circle {
  stroke: #0C3EE3;
  transition: stroke-dashoffset 1s ease-out;
}

.js-circle {
  stroke-linecap: round;

}

.score-empty {
  stroke: #f8f8f8;
}

.score-text {
  fill: #1e1935;
  font-size:60px;
  font-weight:bold;
}
.score-text.small {
  fill:#5E6D7C;
  font-size:25px;
}